import cTitle from 'components/title';
export default {
    data() {
        return {
            sort: '',
            //我的推荐人

            avatar: '',
            level: '',
            nickname: '',
            uid: '',
            //我推荐的人
            my_agent: []

        }
    },
    methods: {
        toggle(event, n) {

            if (event.target.className == 'fa fa-angle-down') {
                this.sort = 0;
                event.target.className = 'fa fa-angle-right';
            } else {
                event.target.className = 'fa fa-angle-down';
                this.sort = n;
            }

        },
        getNetData() {

            var that = this;
            $http.get('member.member.getMyRelation', '').then(function (response) {

                if (response.result == 1) {
                    var mydata = response.data;
                    that.my_agent = mydata.my_agent;
                    //我的推荐人
                    that.activated = mydata.my_referral.avatar;
                    that.level = mydata.my_referral.level;
                    that.nickname = mydata.my_referral.nickname;
                    that.uid = mydata.my_referral.uid;

                }

            }, function (response) {
                // error callback
            });

        }
    },
    activated() {
        this.getNetData();
    },
    components: { cTitle }
}


